.contactsBlock {
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: #333;
    min-height: 100vh;
    color: #b46e3c;

    .contacts {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 20px;
        width: 100%;
        padding: 20px;

        span {
            font-size: 20px;
            font-weight: 100;
            letter-spacing: 5px;
        }

        .contactsForm {
            display: flex;
            flex-direction: column;
            width: 100%;

            & input,
            & textarea {
                box-sizing: border-box;
                border: none;
                border-left: 1px solid rgba(255, 255, 255, 0.3);
                border-right: 1px solid rgba(255, 255, 255, 0.3);
                color: #b46e3c;
                line-height: inherit;
                padding: 6px 20px;
                width: 100%;
                font-size: 14px;
                background: #222;
                margin-bottom: 25px;
                outline: none;
                text-align: center;
                transition: all 0.5s ease 0s;
            }
            .error {
                box-shadow: 0 2px 5px #ff0000;
            }
            :focus{
                box-shadow: 0 2px 5px #b46e3c;
            }         

            .sendBtn {
                color: #b46e3c;
                border: 1px solid #222;
                background: #222;
                letter-spacing: 2px;
                padding: 10px 15px;
                text-transform: uppercase;
                line-height: 17px;
                position: relative;
                z-index: 1;
                overflow: hidden;
                &:hover {
                    color: #fff;
                }
                &::after {
                    position: absolute;
                    content: "";
                    right: 0;
                    top: -100%;
                    width: 100%;
                    height: 100%;
                    background-color: #b46e3c;
                    transition: all 0.8s;
                    z-index: -1;
                }
                &:hover::after {
                    top: 0;
                }
            }
        }
    }
    @media (max-width: 788px) {
        .contacts {
            grid-template-columns: 1fr;
            text-align: center;
            & span {
                font-size: 14px;
            }
        }

        .contactsForm {
            margin-top: 20px;
        }
    }
}
